body{
    background-color: aqua;
   margin: 0;
  
}
.test_row1{
    background-color: beige;
    margin: 100px 30%;
    text-align: center;
    padding: 20px;
    height: 80vh;
   
}
.test_row1_text{
  margin-bottom: 10px;
  font-weight: bolder;
  font-size: larger;
   
}

.test_row1_text1{
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 10px;

   
}
.test_row1_text1 input{
    width: 100%;
    margin-right: 10px;
    border: none;
    outline: none;
    
}

.test_row1_text1 button{
    background-color: brown;
    color: white;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 5px 10px;
}
.test_row1_text1 button:hover{
    background-color: rgb(231, 126, 126);
    color:black;
    font-weight: bold;
    
}


.test{
  display: flex; 
  flex-direction:row;
  width: 100%;
  
  margin-bottom: 10px;
  justify-content: center;
 
}
.test_row2{
    background-color: rgb(164, 136, 136);
    padding-right: 10px;
    width: 30%;
}
.test button{
    background-color: rgb(147, 170, 192);
    border-radius: 3px;
    border: none;
    outline: none;

  
}
.test button:hover{
    background-color: rgb(103, 165, 223);
}

